import React, { useState } from 'react';

import Main from '../layouts/Main';

const InterestCalculatorPage = () => {
  const [interestRate, setInterestRate] = useState(0);
  const [years, setYears] = useState(0);
  const [investment, setInvestment] = useState(0);
  const [result, setResult] = useState(0);

  const calculateInterest = () => {
    const calculatedResult = interestRate ** years * investment;
    setResult(calculatedResult);
  };

  return (
    <Main title='Interest Calculator' description='Calculates investment interest return for a given time'>
      <article className='post' id='interest-calculator'>
        <header>
          Interest Calculator
        </header>
        <article>
          <div>
            <label htmlFor='interestRate'>Interest Rate:</label>
            <input
              type='number'
              id='interestRate'
              value={interestRate}
              onChange={(e) => setInterestRate(parseFloat(e.target.value))}
            />
          </div>

          <div>
            <label htmlFor='years'>Years:</label>
            <input
              type='number'
              id='years'
              value={years}
              onChange={(e) => setYears(parseInt(e.target.value, 10))}
            />
          </div>

          <div>
            <label htmlFor='investment'>Investment:</label>
            <input
              type='number'
              id='investment'
              value={investment}
              onChange={(e) => setInvestment(parseInt(e.target.value, 10))}
            />
          </div>

          <button onClick={calculateInterest}>Calculate</button>

          <div>
            <h2>Result:</h2>
            <p>{result}</p>
          </div>
        </article>
      </article>
    </Main>
  );
};

export default InterestCalculatorPage;
